
:root{
  --text-color: #223548;
  --gradient-start: #9796F0;
  --gradient-end: #FBC7D4;
  --default-border-radius: .6rem;

  --default-box-shadow: 0px 4px 4px -1px rgba(131, 150, 168, .5);
}

html,body{
  font-family: 'Roboto';
  font-weight: 300;

}

@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;500&display=swap');


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.app{
  padding: 2rem;
  background: linear-gradient(var(--gradient-start), var(--gradient-end));
  min-height: 100vh;
}

.app > div{
  max-width: 30rem;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 1rem;
}

.title-container{
  display: flex;
  column-gap: 2rem;
  row-gap: 1rem;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
}

.title-container > img{
  border-radius: var(--default-border-radius);
  width: 8rem;
  box-shadow: var(--default-box-shadow);
}

.description{
  min-width: 10rem;
  max-width: 15rem;
  background-color: white;
  border-radius: var(--default-border-radius);
  padding: 1rem 2rem;
  box-shadow: var(--default-box-shadow);
}

.description > h1{
  font-size: 1.2rem;
  font-weight: 500;
  color: var(--text-color);
  margin-bottom: 1rem;
  font-size: 14px;
}

.description > p {
  color: var(--text-color);
  font-size: 10px;
}

.tracklist-container{
  width: 100%;
  max-width: 30rem;
}

.ctable{
  display: grid;
  grid-template-columns: 1fr 1fr;
  font-size: 10px;
}

.ctable > div{
  background: white;
  padding: .2rem 1rem;
}

.ctable div:nth-child(1),.ctable div:nth-child(2){
  background: #F8F8F8;
  font-weight: 500;
  padding: 0.4rem 1rem;
}

.ctable div:nth-child(1){
  border-top-left-radius: var(--default-border-radius);
}

.ctable div:nth-child(2){
  border-top-right-radius: var(--default-border-radius);
}

.ctable div:last-child{
  border-bottom-right-radius:  var(--default-border-radius);
}

.ctable div:nth-last-child(2){
  border-bottom-left-radius:  var(--default-border-radius);
}

